import {createApp} from 'vue'
import App from './App.vue'
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
//@ts-ignore
// import VueYandexMetrika from 'vue-yandex-metrika'
import {createPinia} from "pinia";
// import { PiniaLogger } from "pinia-logger";
import "v-calendar/dist/style.css";
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
//@ts-ignore
import egalWidgets from '@egalteam/widget-library';
import '@egalteam/widget-library/dist/style.css';
import "bootstrap/dist/css/bootstrap.min.css";
import 'bootstrap/dist/js/bootstrap.min.js';
import "@/assets/fonts/raleway/raleway.css";
import 'vue-universal-modal/dist/index.css'
import VueUniversalModal from 'vue-universal-modal'
import axios from 'axios'
import VCalendar from "v-calendar";
import SetupCalendar from "v-calendar";
const pinia = createPinia();
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import VueAxios from 'vue-axios';

// Import the functions you need from the SDKs you need
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import "firebase/compat/firestore";
import { getAnalytics } from "firebase/analytics";

const firebaseConfig = {
    apiKey: "AIzaSyBr4RomrmBNFfYQNnkOiaQgZri8G5W_4vA",
    authDomain: "sputnik-mconnection-test.firebaseapp.com",
    projectId: "sputnik-mconnection-test",
    storageBucket: "sputnik-mconnection-test.appspot.com",
    messagingSenderId: "1037983478798",
    appId: "1:1037983478798:web:ab90add58a832fca90385f",
    measurementId: "G-02146NF8WE"
};

// Initialize Firebase
const app = firebase.initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

// Use these for db & auth
const db = app.firestore();
const auth = app.auth();

export { auth, db };

createApp(App)
    .use(SetupCalendar, {})
    .use(egalWidgets)
    .use(VueAxios, axios)
    .use(pinia)
    .use(VCalendar, {
        screens: {
            tablet: "576px",
            laptop: "992px",
            desktop: "1200px",
        },
    })
    .use(VueUniversalModal, {
    teleportTarget: '#modals',
    modalComponent: 'Modal',
},)
.mount('#app');

