<template>
  <DefaultLayout>
    <div class="banner">
      <p class="title m-0">Тест Клиента</p>
    </div>

    <div class="box">
      <div class="container_box" :class="activeTest===0 ? 'footer-wrap' : ''">
        <div class="test_form">

          <div class="alert-danger p-2 mt-2 position-relative" v-if="error"
               style="background: rgba(189,203,243, 0.5); color: #111111">{{ error }}
          </div>

          <template v-if="activeTest===0">
            <h1 class="title mt-4" style="color: #C381D9">Тестирование завершено</h1>
            <p class="test_form--desc">Можете закрыть страницу,
              через неделю после вашей первой встречи мы направим вам вторую часть анкеты на почту, чтобы вы оставили
              свою обратную связь о совместной работе. </p>
          </template>

          <template v-if="activeTest===1">
            <p class="test_form--desc">Нам необходимо получить входные данные для тестирования методики.</p>
            <p class="test_form--desc">Мы гарантируем, что собранные нами данные будут использованы исключительно для
              исследовательских целей и не будут переданы третьим лицам.</p>
          </template>

          <template v-if="activeTest===2">
            <p class="test_form--desc">Ниже будут представлены вопросы, и вам нужно будет выбрать наиболее подходящий
              для вас ответ.</p>
          </template>

          <template v-if="activeTest===3">
            <p class="test_form--desc">В данном разделе вам необходимо выбрать один из приведенных вариантов ответа,
              наиболее точно описывающий вашу позицию</p>
          </template>

          <template v-if="activeTest===4">
            <p class="test_form--desc">Тестирование на темперамент и социотип</p>
          </template>

          <div class="preloader p-5" v-if="questions.length===0">
            <Preloader></Preloader>
          </div>


          <form type="submit" class="test_form--form-box" @submit.prevent="()=>{}" @click="startTimer">

            <template v-for="(question, index) in this.questions" :key="index">

              <div class="first_stage p-0"
                   v-show="activeTest === question.page"
                   :style="{visibility: activeTest === question.page ? 'visible' : 'hidden', height: activeTest === question.page ? 'auto' : 0}">
                <Questions :question="question" @answer-set="updateAnswers" :is-valid="startValidation"></Questions>
              </div>

            </template>

            <div class="d-flex gap-3 mt-4">
              <e-button
                  v-if="activeTest > 1"
                  @click="prevStage"
                  class="mb-3"
                  :data="{ rounded: true }"
                  :styleConfig="buttons.styleDarkButton"
              >Назад
              </e-button>

              <e-button
                  type="submit"
                  v-if="0 < activeTest && activeTest < 4"
                  @click="nextStage"
                  class="mb-3"
                  :data="{ rounded: true }"
                  :styleConfig="buttons.styleDarkButton"
              >Далее
              </e-button>

              <e-button
                  type="submit"
                  v-if="activeTest === 4"
                  @click="setData(false)"
                  class="mb-3"
                  :data="{ rounded: true }"
                  :styleConfig="buttons.styleDarkButton"
              >Отправить
              </e-button>

            </div>
          </form>
        </div>
      </div>
    </div>
  </DefaultLayout>
</template>

<script>
import {buttons, inputs} from "@/assets/egalStyles/style";
import DefaultLayout from "@/layouts/BaseLayout";
import firebase from "firebase/compat/app";
import Questions from "@/components/Questions";
import {arrayUnion, doc, updateDoc, arrayRemove} from "firebase/firestore";
import {db} from "./main";
import {validEmail} from "@/assets/egal/validations";
import Preloader from "@/layouts/Preloader";
import ActionConstructor from "@/assets/helpers/actionConstructorModule";
import axios from "axios";

export default {
  name: "TestClient",
  components: {
    DefaultLayout,
    Questions,
    Preloader
  },
  computed: {
    firstStageValidation() {
      return this.requiredNumbers === this.answers.filter(answer => answer.required === true && answer.answer.length > 0).length;
    },
    validationQuestion(question) {
      return question.title === 'Укажите ФИО психолога, к которому вы планируете обратиться' ? this.model.fio : question.title === 'Ваша почта' ? this.model.email : this.model.dateOfMeet
    }
  },
  data: () => {
    return {
      inputs,
      buttons,
      model: {
        fio: "",
        email: "",
        dateOfMeet: "",
      },
      questions: [],
      error: '',
      activeTest: 1,
      answers: [],
      requiredNumbers: 0,
      tempTimer: 0,
      failures: [],
      generalFailures: [],
      leaveQuestion: {},
      isBlur: true,
      startValidation: false,
    }
  },
  methods: {
    scrollToTop() {
      window.scrollTo(0, 0);
    },

    startTimer() {
      if (this.tempTimer === 0) setInterval(() => this.tempTimer++, 1000);
      this.tempTimer++;
    },

    updateAnswers(oneAnswer) {
      oneAnswer.time = this.tempTimer;
      let isIncludes = this.answers.findIndex(answer => answer.question === oneAnswer.question);
      if (isIncludes === -1) {
        this.tempTimer = 0;
        this.answers.push(oneAnswer);
      } else this.answers[isIncludes] = oneAnswer;
    },

    validateEmail() {
      let email = this.answers.find(answer=>answer.question === "Ваша почта");
      this.error = validEmail(email?.answer ?? '');
      this.startValidation = true;
    },

    nextStage() {
      this.scrollToTop();
      this.validateEmail()

      if (!this.firstStageValidation) {
        this.error = "Заполните все обязательные поля";
        return;
      }

      if (this.error.length > 0) return;

      this.startValidation = false;
      this.error = '';
      this.activeTest++;
    },

    prevStage() {
      this.activeTest--;
    },

    getRequired(questions) {
      return questions.filter(question => question.required === true);
    },

    setPages(querySnapshot) {
      querySnapshot.forEach((doc) => {
        if (doc.id === "questions-cllient") {
          this.questions = doc.data().questions;
        }
      })
      this.requiredNumbers = this.getRequired(this.questions).length;

    },

    async sentEmail() {
      let email = this.answers.find(answer => answer.question === "Ваша почта");

      if (email) {
        if (validEmail(email.answer) !== '') return;

        await axios.post(process.env.VUE_APP_API_URL + '/core/Notification/create', {
          attributes: {
            email: email.answer,
          }
        })
      }
    },

    async setData(isLeave) {
      if (!isLeave) {
        this.validateEmail();
        if (this.error.length > 0) return;
      }
      let pathSegments = isLeave ? "leave-answers" : "client-answers";

      const data = doc(db, "answers", pathSegments);
      await updateDoc(data, {
        questions: arrayUnion({user: [...this.answers]})
      })
      if (isLeave) return;
      this.activeTest = 0;
      this.scrollToTop();
      await this.sentEmail();

    },

    getData() {
      firebase.firestore().collection('questions').get().then((querySnapshot) => {
        this.setPages(querySnapshot);
      })
    },

    setDataFailures(leaveQuestion) {
      let overlap = this.failures.findIndex(over => over.title === leaveQuestion.title);

      if (overlap === -1) {
        leaveQuestion.failuresCounter = 0;
        this.setFailures(leaveQuestion);
      } else {
        leaveQuestion.failuresCounter = (this.failures[overlap].failuresCounter)+1;
        this.setFailures(leaveQuestion);
      }
    },

    getFailures(querySnapshot) {
      querySnapshot.forEach((doc) => {
        if (doc.id === "client-failures") {
          this.failures = doc.data().failures;
        }
      })
    },

    getDataFailures() {
      firebase.firestore().collection('statistics').get().then((querySnapshot) => {
        this.getFailures(querySnapshot);
      })
    },

     setFailures(leaveQuestion) {
      let deleteLeaveQuestion = Object.assign({}, leaveQuestion);
      deleteLeaveQuestion.failuresCounter--;

       const data = doc(db, "statistics", "client-failures");
       const generalData = doc(db, "statistics", "general-failures");

       if (deleteLeaveQuestion.failuresCounter >=0) {
         updateDoc(data, {
           failures: arrayRemove(deleteLeaveQuestion)
         })
         updateDoc(generalData, {
           failures: arrayRemove(deleteLeaveQuestion)
         })
       }

       updateDoc(data, {
        failures: arrayUnion(leaveQuestion)
      })
       updateDoc(generalData, {
        failures: arrayUnion(leaveQuestion)
      })

      this.failures = [];
      this.generalFailures = [];
      this.getDataFailures();
    },

    leaveCounterIncrease() {
      let answer;
      if (this.answers.length > 0) {
        answer = this.answers[this.answers.length - 1];
        this.leaveQuestion = this.questions[this.questions.findIndex(question => question.title === answer.question) + 1];
      } else {
        this.leaveQuestion = this.questions[0];
      }
      if (this.leaveQuestion) {
        this.setDataFailures(this.leaveQuestion);
      }
      this.setData(true);
      this.sentEmail();
    }

  },
  async mounted() {
    this.scrollToTop();
    await this.getData();
    this.getDataFailures();

    document.addEventListener('mouseleave', e=>{
      if (this.isBlur) {
        this.leaveCounterIncrease();
        this.isBlur = false;
      }
    })
  },
}
</script>

<style scoped lang="scss">

.title {
  font-size: 2rem;
  font-weight: 600;
  margin-top: 1rem;
  color: white;
}


.box {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container_box {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero, .banner {
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.yandex_box {
  min-height: 40vh !important;
}

.test_form--desc {
  font-size: 20px;
  color: #C381D9;
  font-weight: 520;
  padding: 0.5rem 0;
  margin-top: 1rem;
  margin-bottom: 0;
}

.test_form--form-box {
  display: flex;
  flex-direction: column;
}

.test_form--form-box *:not(p) {
  width: 100%;
}

.e-radio {
  padding: 0.2rem 0;
}

span.e-radio--left .text {
  display: block !important;
  gap: 1rem !important;
  flex-direction: column !important;
}

.banner {
  background: #bdcbf3;
}

.footer-wrap {
  min-height: 60vh;
}

@media screen and (max-width: 1000px) {
  .banner {
    margin-bottom: 0 !important;
    padding: 0.5rem;
    height: auto;
    background: #bdcbf3;
  }

  .container_box {
    width: 90%;
  }

  .test_form--form-box *:not(p) {
    width: 100%;
  }

  .test_form--desc {
    margin-top: 0;
  }
}

</style>